import React, { ReactNode, forwardRef, Ref, useImperativeHandle, useRef } from 'react';
import { frGetter, autoRows, formatAreas } from './functions';

export interface IGridLayout {
  columns: number | string;
  rows?: number | string;
  gap?: string | number;
  columnGap?: string | number;
  areas?: string[];
  minRowHeight?: string;
  alignContent?: string;
  rowGap?: string | number;
  justifyContent?: string;
  flow?: string;
  height?: string;
  style?: Record<string, any>;
  children: ReactNode;
  className?: any;
}

function GridLayout(props: IGridLayout, ref: Ref<HTMLDivElement | null>) {
  const {
    columns,
    gap = '8px',
    columnGap,
    areas,
    minRowHeight,
    alignContent,
    rowGap,
    rows,
    justifyContent,
    flow = 'row',
    children,
    height = 'auto',
    style = {},
    className
  } = props
  const gridRef = useRef<HTMLDivElement>(null)
  const mergedstyle: Record<string, any> = {
    display: 'grid', // grid
    height, // 
    gridAutoFlow: flow, // 
    gridAutoRows: autoRows(minRowHeight), // grid-auto-rows
    gridTemplateRows: frGetter(rows), // 100px
    gridTemplateColumns: frGetter(columns), //   12100px
    columnGap, // 
    rowGap, // 
    gridTemplateAreas: formatAreas(areas), //  [a ab c] 
    justifyContent, // ()
    alignContent, // ()
    ...style,
  };
  if (gap) mergedstyle.gap = gap; // 8px

  useImperativeHandle(ref, () => gridRef.current, [])

  return (
    <div ref={gridRef} className={className} style={mergedstyle}>
      {children}
    </div>
  );
};

export default React.memo(forwardRef(GridLayout));